import React from 'react';

/**
 * Returns whether the focus is currently within one of the elements in elementRefs.
 * @param elementRefs Element or list of elements to check the focus on
 * @param opts Options object
 */
export function useIsFocusWithin(elementRefs: React.RefObject<HTMLElement> | Array<React.RefObject<HTMLElement>>) {
  const [isFocusWithin, setIsFocusWithin] = React.useState<boolean>(false);
  const els = Array.isArray(elementRefs) ? elementRefs : [elementRefs];

  React.useEffect(() => {
    const onFocus = (e: FocusEvent) => {
      const target = e.target as HTMLElement;
      let hasFocus = false;
      for (const el of els) {
        if (el.current?.contains(target)) {
          hasFocus = true;
          break;
        }
      }

      setIsFocusWithin(hasFocus);
    };

    document.addEventListener('focusin', onFocus);
    return () => {
      document.removeEventListener('focusin', onFocus);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [els.length]);

  return isFocusWithin;
}
